import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import yonghuList from '@/views/pages/yonghu/list'
import yonghuDetail from '@/views/pages/yonghu/formModel'
import yonghuAdd from '@/views/pages/yonghu/formAdd'
import yonghuRegister from '@/views/pages/yonghu/register'
import yonghuCenter from '@/views/pages/yonghu/center'
import xinwengonggaoList from '@/views/pages/xinwengonggao/list'
import xinwengonggaoDetail from '@/views/pages/xinwengonggao/formModel'
import xinwengonggaoAdd from '@/views/pages/xinwengonggao/formAdd'
import shipinleixingList from '@/views/pages/shipinleixing/list'
import shipinleixingDetail from '@/views/pages/shipinleixing/formModel'
import shipinleixingAdd from '@/views/pages/shipinleixing/formAdd'
import wanjushipinList from '@/views/pages/wanjushipin/list'
import wanjushipinDetail from '@/views/pages/wanjushipin/formModel'
import wanjushipinAdd from '@/views/pages/wanjushipin/formAdd'
import forumList from '@/views/pages/forum/list'
import wanjufenleiList from '@/views/pages/wanjufenlei/list'
import wanjufenleiDetail from '@/views/pages/wanjufenlei/formModel'
import wanjufenleiAdd from '@/views/pages/wanjufenlei/formAdd'
import wanjuxinxiList from '@/views/pages/wanjuxinxi/list'
import wanjuxinxiDetail from '@/views/pages/wanjuxinxi/formModel'
import wanjuxinxiAdd from '@/views/pages/wanjuxinxi/formAdd'
import storeupList from '@/views/pages/storeup/list'
import cartList from '@/views/pages/shop_order/cart'
import addressList from '@/views/pages/shop_address/list'
import wanjuzulinList from '@/views/pages/wanjuzulin/list'
import wanjuzulinDetail from '@/views/pages/wanjuzulin/formModel'
import wanjuzulinAdd from '@/views/pages/wanjuzulin/formAdd'
import wanjuguihaiList from '@/views/pages/wanjuguihai/list'
import wanjuguihaiDetail from '@/views/pages/wanjuguihai/formModel'
import wanjuguihaiAdd from '@/views/pages/wanjuguihai/formAdd'
import order_confirm from '@/views/pages/shop_order/confirm'
import ordersList from '@/views/pages/shop_order/list'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'xinwengonggaoList',
			component: xinwengonggaoList
		}, {
			path: 'xinwengonggaoDetail',
			component: xinwengonggaoDetail
		}, {
			path: 'xinwengonggaoAdd',
			component: xinwengonggaoAdd
		}
		, {
			path: 'shipinleixingList',
			component: shipinleixingList
		}, {
			path: 'shipinleixingDetail',
			component: shipinleixingDetail
		}, {
			path: 'shipinleixingAdd',
			component: shipinleixingAdd
		}
		, {
			path: 'wanjushipinList',
			component: wanjushipinList
		}, {
			path: 'wanjushipinDetail',
			component: wanjushipinDetail
		}, {
			path: 'wanjushipinAdd',
			component: wanjushipinAdd
		}
		, {
			path: 'forumList',
			component: forumList
		}
		, {
			path: 'wanjufenleiList',
			component: wanjufenleiList
		}, {
			path: 'wanjufenleiDetail',
			component: wanjufenleiDetail
		}, {
			path: 'wanjufenleiAdd',
			component: wanjufenleiAdd
		}
		, {
			path: 'wanjuxinxiList',
			component: wanjuxinxiList
		}, {
			path: 'wanjuxinxiDetail',
			component: wanjuxinxiDetail
		}, {
			path: 'wanjuxinxiAdd',
			component: wanjuxinxiAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'cartList',
			component: cartList
		}
		, {
			path: 'addressList',
			component: addressList
		}
		, {
			path: 'wanjuzulinList',
			component: wanjuzulinList
		}, {
			path: 'wanjuzulinDetail',
			component: wanjuzulinDetail
		}, {
			path: 'wanjuzulinAdd',
			component: wanjuzulinAdd
		}
		, {
			path: 'wanjuguihaiList',
			component: wanjuguihaiList
		}, {
			path: 'wanjuguihaiDetail',
			component: wanjuguihaiDetail
		}, {
			path: 'wanjuguihaiAdd',
			component: wanjuguihaiAdd
		}
		, {
			path: 'order_confirm',
			component: order_confirm
		}
		, {
			path: 'ordersList',
			component: ordersList
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
